define([
    "dojo/Deferred",
    "esri/tasks/QueryTask",
    "esri/tasks/query",
    "esri/tasks/StatisticDefinition",
    "esri/geometry/geometryEngine",
    "dojo/promise/all",
    "esri/request"
], function (
    Deferred,
    QueryTask,
    Query,
    StatisticDefinition,
    geometryEngine,
    all,
    esriRequest
) {
    /*
    * @description: Objeto que contiene las funciones para la subdivisi贸n de lotes
    */
    const UtilityCase = {

        ubigeoFieldName: 'UBIGEO',
        platformUpdate: 'PCF',
        estadoInsValue: 1,
        estadoValue: 1,
        codUiValue: 1,

        Land() {
            this.ubigeo = 'UBIGEO';
            this.codPre = 'COD_PRE';
            this.codUi = 'COD_UI';
            this.estado = 'ESTADO';
            this.coordX = 'COORD_X';
            this.coordY = 'COORD_Y';
            this.codVer = 'COD_VER';
            this.codCpu = 'COD_CPU';
            this.dirMun = 'DIR_MUN';
            this.dirUrb = 'DIR_URB';
            this.ranCpu = 'RAN_CPU';
            this.tipVia = 'TIP_VIA';
            this.nomVia = 'NOM_VIA';
            this.numMun = 'NUM_MUN';
            this.idMznC = 'ID_MZN_C';
            this.idPred = 'ID_PRED';
            this.tipPred = 'TIP_PRED';
        },
        PointLot() {
            this.ubigeo = 'UBIGEO';
            this.idLote = 'ID_LOTE'
            this.secuen = 'SECUEN';
            this.coordX = 'COORD_X';
            this.coordY = 'COORD_Y';
            this.zonaUtm = 'ZONA_UTM';
            this.estadoIns = 'ESTADO_INS'
        },
        Lot() {
            this.idLotP = 'ID_LOTE_P';
            this.ranCpu = 'RAN_CPU';
            this.anoCart = 'ANO_CART';
            this.fuente = 'FUENTE';
            this.nomPc = 'NOM_PC';
            this.nomUser = 'NOM_USER';
            this.codLot = 'COD_LOTE';
            this.lotUrb = 'LOT_URB';
            this.ubigeo = 'UBIGEO';
            this.tipLot = 'TIP_LOT';
            this.estadoIns = 'ESTADO_INS';
        },
        Arancel() {
            this.secEjec = 'SEC_EJEC';

        },

        receptionModelRequest() {
            return [
                "COD_PRE",
                "COD_CPU",
                "COD_SECT",
                "COD_MZN",
                "COD_LOTE",
                "COD_UU",
                "COD_VIA",
                "TIPO_UU",
                "NOM_UU",
                "NOM_REF",
                "MZN_URB",
                "LOT_URB",
                "TIP_VIA",
                "NOM_VIA",
                "CUADRA",
                "LADO",
                "DIR_MUN",
                "DIR_URB",
                "COORD_X",
                "COORD_Y",
                "RAN_CPU",
                "COD_UI",
                "COD_VER",
                "ID"
            ]
        },

        matchWithReceptionModel(object) {
            const modelRequests = this.receptionModelRequest();
            const response = object.map((land) => {
                const arrayMatch = {};
                modelRequests.forEach((field) => {
                    arrayMatch[field] = land.attributes[field];
                })
                return arrayMatch;
            })
            return response;
        },

        getFeatureSchema(url, geometry = true, blankFields = true) {
            const deferred = new Deferred();
            const queryTask = new QueryTask(url);
            const query = new Query();
            query.where = "1=1";
            query.returnGeometry = geometry;
            query.outFields = ["*"];
            query.num = 1;
            queryTask.execute(query)
                .then(result => {
                    const feature = result.features[0];
                    if (blankFields) {
                        for (let prop in feature.attributes) {
                            feature.attributes[prop] = null;
                        }
                    }
                    return deferred.resolve(feature);
                })
                .catch(err => {
                    deferred.reject(err);
                });
            return deferred.promise;
        },

        attributeTransfer({ objTarget, objBase, propsUse = [], propsOmit = [], updateOnlyNulls = false, omitPropsDefault = true, deletePropsDefault = true }) {
            let fieldMatch = []
            const propsOmitDefault = [
                'FUENTE',
                'NOM_PC',
                'NOM_USER',
                'ANO_CART'
            ]
            const propsDeleteDefault = [
                'OBJECTID',
                'GlobalID',
                'created_date',
                'created_user',
                'last_edited_date',
                'last_edited_user',
                'Shape.STArea()',
                'Shape.STLength()',
            ];

            if (deletePropsDefault) {
                for (let prop in objTarget) {
                    if (propsDeleteDefault.includes(prop)) {
                        delete objTarget[prop];
                    }
                }
            }

            if (propsUse.length > 0) {
                fieldMatch = propsUse
            } else {
                const props1 = Object.keys(objTarget);
                const props2 = Object.keys(objBase);
                fieldMatch = props1.filter(prop => props2.includes(prop));
            }
            // retirar los campos omitidos
            if (propsOmit.length > 0) {
                fieldMatch = fieldMatch.filter(prop => !propsOmit.includes(prop));
            }

            // retirar los campos omitidos por defecto de objBase
            if (omitPropsDefault) {
                fieldMatch = fieldMatch.filter(prop => !propsOmitDefault.includes(prop));
            }

            fieldMatch.forEach(prop => {
                if (objBase.hasOwnProperty(prop)) {
                    if (updateOnlyNulls) {
                        if (objTarget[prop] === null) {
                            objTarget[prop] = objBase[prop];
                        }
                    } else {
                        objTarget[prop] = objBase[prop];
                    }
                }
            });

            return objTarget;
        },

        getValueCodVer(ranCpu, codUi) {
            const factor = [2, 3, 4, 5, 6, 7, 2, 3, 4, 5, 6, 7]
            // Obteniendo c贸digo concatenado
            const cod_ver_concatenate = ranCpu.toString() + ("0000" + codUi.toString()).slice(-4);

            // Reversa de c贸digo concatenado
            const cod_ver = cod_ver_concatenate.split('').reverse().join('');

            // Aplicando f贸rmula
            let response = 11 - (cod_ver.split('').map((digit, index) =>
                parseInt(digit) * factor[index])
                .reduce((a, b) => a + b, 0) % 11);

            if (response > 9) {
                response = 11 - response;
            }

            return response
        },

        getBlockFromLot(geometry, url) {
            const deferred = new Deferred();

            const queryBlock = new Query();
            queryBlock.geometry = geometry;
            queryBlock.outFields = ['*'];
            queryBlock.returnGeometry = true;
            const queryTaskBlock = new QueryTask(url);
            queryTaskBlock.execute(queryBlock)
                .then(response => {
                    if (response.features.length === 0) {
                        return deferred.reject("No se encontraron manzanas");
                    }
                    return deferred.resolve(response.features[0]);
                })
                .catch(err => deferred.reject(err));

            return deferred.promise;
        },

        checkExistLotUrban(attributes, block, urlLots) {
            const deferred = new Deferred();
            const queryLot = new Query();
            queryLot.geometry = block.geometry
            queryLot.spatialRel = "within";
            const LotCls = new this.Lot();
            queryLot.outFields = [LotCls.lotUrb];
            const queryTaskLot = new QueryTask(urlLots);
            const lotsUrban = attributes.map(attr => attr.lotUrb);
            queryTaskLot.execute(queryLot)
                .then(response => {
                    const lots = response.features.map(lot => lot.attributes[LotCls.lotUrb]);
                    const exist = lotsUrban.some(lot => lots.includes(lot));
                    if (exist) {
                        return deferred.reject("Los lotes urbanos registrados ya se encuentran en la manzana actual");
                    }
                    return deferred.resolve(block);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        translateFieldsBlockToLot(url, block, lotsResults) {
            const deferred = new Deferred();

            this.getFeatureSchema(url)
                .then(lot => {
                    const lots = lotsResults.map((graphic) => {
                        const lotIdx = lot.clone();
                        lotIdx.attributes = this.attributeTransfer({
                            objTarget: lotIdx.attributes,
                            objBase: block.attributes,
                        });
                        lotIdx.geometry = graphic.geometry;
                        return lotIdx;
                    });
                    return deferred.resolve(lots);
                })
                .catch(err => deferred.reject(err));

            return deferred.promise;
        },

        calculateTipLot(currentLotsRows) {
            const LotCls = new this.Lot();
            let tipLot = currentLotsRows.map(row => { return row.attributes[LotCls.tipLot] })
            tipLot = [...new Set(tipLot)] == [2] ? '2' : '1'
            return tipLot
        },

        calculateFieldsOfLot(lotUrl, lots, ubigeo, codRequests, user, attributes, tipLot) {
            const deferred = new Deferred();
            const LotCls = new this.Lot();

            const queryLotTask = new QueryTask(lotUrl);

            const queryLot = new Query();

            queryLot.where = `${this.ubigeoFieldName} = '${ubigeo}'`;

            const statDefIdLoteP = new StatisticDefinition();
            statDefIdLoteP.statisticType = "max";
            statDefIdLoteP.onStatisticField = LotCls.idLotP;
            statDefIdLoteP.outStatisticFieldName = `${LotCls.idLotP}_MAX`;

            const statDefRanCpu = new StatisticDefinition();
            statDefRanCpu.statisticType = "max";
            statDefRanCpu.onStatisticField = LotCls.ranCpu;
            statDefRanCpu.outStatisticFieldName = `${LotCls.ranCpu}_MAX`;

            queryLot.returnGeometry = false;
            queryLot.outStatistics = [statDefIdLoteP, statDefRanCpu];

            queryLotTask.execute(queryLot)
                .then(response => {
                    lots.forEach((lot, idx) => {
                        lot.attributes[LotCls.idLotP] = response.features[0].attributes[statDefIdLoteP.outStatisticFieldName] + idx + 1;
                        lot.attributes[LotCls.ranCpu] = response.features[0].attributes[statDefRanCpu.outStatisticFieldName] + idx + 1;
                        lot.attributes[LotCls.anoCart] = new Date().getFullYear();
                        lot.attributes[LotCls.fuente] = codRequests;
                        lot.attributes[LotCls.nomPc] = this.platformUpdate;
                        lot.attributes[LotCls.nomUser] = user;
                        lot.attributes[LotCls.codLot] = attributes[idx].codLot;
                        lot.attributes[LotCls.lotUrb] = attributes[idx].lotUrb;
                        lot.attributes[LotCls.tipLot] = tipLot;
                        lot.attributes[LotCls.estadoIns] = this.estadoInsValue;
                    });
                    return deferred.resolve(lots);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        translateFieldsLotToPointLot(lots, urlPointLots, newPointLotsGraphics) {
            const deferred = new Deferred();
            const pointLots = [];

            this.getFeatureSchema(urlPointLots)
                .then(pointLot => {
                    lots.forEach(lot => {
                        pointLot.attributes = this.attributeTransfer({
                            objTarget: pointLot.attributes,
                            objBase: lot.attributes,
                            omitPropsDefault: false,
                            deletePropsDefault: true
                        });

                        for (let graph of newPointLotsGraphics) {
                            if (!geometryEngine.intersects(lot.geometry, graph.geometry)) {
                                continue;
                            }

                            const pointLotIdx = pointLot.clone();
                            pointLotIdx.geometry = graph.geometry;
                            pointLots.push(pointLotIdx);
                        };
                    });
                    return deferred.resolve(pointLots);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        translateFieldsArancelToPointLot(pointLots, arancelUrl) {
            const deferred = new Deferred();
            const promises = pointLots.map(pointLot => {
                const queryArancelTask = new QueryTask(arancelUrl);
                const queryArancel = new Query();
                queryArancel.returnGeometry = true;
                queryArancel.outFields = ["*"];
                queryArancel.geometry = pointLot.geometry;
                queryArancel.geometryType = "esriGeometryPoint";
                return queryArancelTask.execute(queryArancel);
            });
            all(promises)
                .then(arancels => {
                    pointLots.forEach((element, index) => {
                        const arancel = arancels[index].features[0].attributes;
                        pointLots[index].attributes = this.attributeTransfer({
                            objTarget: element.attributes,
                            objBase: arancel,
                            updateOnlyNulls: true
                        });
                        return deferred.resolve(pointLots);
                    });
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        calculateFieldsOfPointLot(pointLotUrl, ubigeo, pointLots) {
            const deferred = new Deferred();
            const PointLotCls = new this.PointLot();

            const queryPointLotTask = new QueryTask(pointLotUrl);

            const queryPointLot = new Query();
            queryPointLot.where = `${PointLotCls.ubigeo} = '${ubigeo}'`
            const statDef = new StatisticDefinition();
            statDef.statisticType = "max";
            statDef.onStatisticField = PointLotCls.secuen;
            statDef.outStatisticFieldName = `${PointLotCls.secuen}_MAX`;

            queryPointLot.returnGeometry = false;
            queryPointLot.outStatistics = [statDef];

            queryPointLotTask.execute(queryPointLot)
                .then(response => {
                    let secuen = response.features[0].attributes[statDef.outStatisticFieldName] + 1
                    for (let i of pointLots) {
                        i.attributes[PointLotCls.secuen] = secuen;
                        i.attributes[PointLotCls.idLote] = `${i.attributes[PointLotCls.zonaUtm]}${ubigeo}${secuen}`;
                        i.attributes[PointLotCls.estadoIns] = this.estadoInsValue;
                        secuen += 1;
                    }
                    return deferred.resolve(pointLots);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        generateCodCpu(ranCpu, codVer, codUi = 1) {
            codUi = ("0000" + codUi.toString()).slice(-4);
            return `${ranCpu}-${codUi}-${codVer}`;
        },

        generateDirMun(tipVia, nomVia, numMun) {
            return `${tipVia} ${nomVia} ${numMun}`;
        },

        generateDirUrb(tipVia, nomVia, numMun) {
            return `${tipVia} ${nomVia} ${numMun}`;
        },

        translateFieldsPointLotToLand(pointLots, landUrl, newLandsGraphics) {
            const deferred = new Deferred();
            const LandCls = new this.Land();
            let lands = [];

            this.getFeatureSchema(landUrl)
                .then(land => {
                    newLandsGraphics.forEach(landGraphic => {
                        for (i = 0; i < pointLots.length; i++) {
                            if (geometryEngine.intersects(landGraphic.geometry, pointLots[i].geometry)) {
                                const landProps = land.clone();
                                landProps.attributes = this.attributeTransfer({
                                    objTarget: land.attributes,
                                    objBase: pointLots[i].attributes,
                                    omitPropsDefault: false
                                });
                                landProps.attributes[LandCls.codPre] = landGraphic.codPre;
                                landProps.attributes[LandCls.codUi] = this.codUiValue;
                                landProps.attributes[LandCls.estado] = this.estadoValue;
                                landProps.attributes[LandCls.coordX] = landGraphic.geometry.x;
                                landProps.attributes[LandCls.coordY] = landGraphic.geometry.y;
                                landProps.attributes[LandCls.codVer] = this.getValueCodVer(
                                    landProps.attributes[LandCls.ranCpu],
                                    this.codUiValue
                                );
                                landProps.attributes[LandCls.codCpu] = this.generateCodCpu(
                                    landProps.attributes[LandCls.ranCpu],
                                    landProps.attributes[LandCls.codVer]
                                );
                                landProps.attributes[LandCls.dirMun] = this.generateDirMun(
                                    landProps.attributes[LandCls.tipVia],
                                    landProps.attributes[LandCls.nomVia],
                                    landProps.attributes[LandCls.numMun]
                                );
                                landProps.attributes[LandCls.dirUrb] = this.generateDirUrb(
                                    landProps.attributes[LandCls.tipVia],
                                    landProps.attributes[LandCls.nomVia],
                                    landProps.attributes[LandCls.numMun]
                                );
                                landProps.geometry = landGraphic.geometry;
                                landProps.attributes['ID'] = parseInt(landGraphic.id);
                                lands.push(landProps.clone());
                                break;
                            }
                        }
                    });
                    return deferred.resolve(lands);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        calculateIdMznC(lands, cadastralBlockUrl, ubigeo) {
            const deferred = new Deferred();
            const LandCls = new this.Land();

            const queryCadastralBlockTask = new QueryTask(cadastralBlockUrl);
            const queryCadastralBlock = new Query();
            queryCadastralBlock.where = `${LandCls.ubigeo} = '${ubigeo}'`;
            queryCadastralBlock.returnGeometry = true;
            queryCadastralBlock.outFields = [LandCls.idMznC];
            queryCadastralBlock.geometry = lands[0].geometry;

            queryCadastralBlockTask.execute(queryCadastralBlock)
                .then(response => {
                    lands.forEach(land => {
                        land.attributes[LandCls.idMznC] = response.features[0].attributes[LandCls.idMznC];
                    });
                    return deferred.resolve(lands);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        calculateIdPred(lands, landUrl, ubigeo) {
            const deferred = new Deferred();
            const LandCls = new this.Land();

            const queryLandTask = new QueryTask(landUrl);
            const queryLand = new Query();
            queryLand.returnGeometry = false;
            queryLand.where = `${LandCls.ubigeo} = '${ubigeo}'`
            const statDef = new StatisticDefinition();
            statDef.statisticType = "max";
            statDef.onStatisticField = LandCls.idPred;
            statDef.outStatisticFieldName = `${LandCls.idPred}_MAX`;
            queryLand.outStatistics = [statDef];

            queryLandTask.execute(queryLand)
                .then(function (response) {
                    lands.forEach((land, idx) => {
                        land.attributes[LandCls.idPred] = response.features[0].attributes[statDef.outStatisticFieldName] + idx + 1;
                    });
                    return deferred.resolve(lands);
                }, function (error) {
                    deferred.reject(error);
                });
            return deferred.promise;
        },

        getPointLotsOrigin(pointLotUrl, lots) {
            const queryPointLotsOriginTask = new QueryTask(pointLotUrl);
            const queryPointLotsOrigin = new Query();
            queryPointLotsOrigin.returnGeometry = true;
            queryPointLotsOrigin.outFields = ["*"];
            queryPointLotsOrigin.geometry = lots.geometry;
            queryPointLotsOrigin.geometryType = "esriGeometryPolygon";
            queryPointLotsOrigin.distance = 0.5;
            queryPointLotsOrigin.units = "meters";
            return queryPointLotsOriginTask.execute(queryPointLotsOrigin);
        },

        getLandsOrigin(landUrl, lots) {
            const queryLandsOriginTask = new QueryTask(landUrl);
            const queryLandsOrigin = new Query();
            queryLandsOrigin.returnGeometry = true;
            queryLandsOrigin.outFields = ["*"];
            queryLandsOrigin.geometry = lots.geometry;
            queryLandsOrigin.geometryType = "esriGeometryPolygon";
            queryLandsOrigin.distance = 0.5;
            queryLandsOrigin.units = "meters";
            return queryLandsOriginTask.execute(queryLandsOrigin);
        },

        getLandsOriginByQuery(landUrl, cpu) {
            const deferred = new Deferred();
            const LandCls = new this.Land();

            const queryLandsOriginTask = new QueryTask(landUrl);
            const queryLandsOrigin = new Query();
            queryLandsOrigin.returnGeometry = true;
            queryLandsOrigin.outFields = ["*"];
            queryLandsOrigin.where = `${LandCls.codCpu} = '${cpu}'`;
            queryLandsOriginTask.execute(queryLandsOrigin)
                .then(result => {
                    return deferred.resolve(result);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        updateRowsGeneric(features, codRequest, user) {
            const LandCls = new this.Land();
            const LotCls = new this.Lot();
            features.forEach(feature => {
                feature.attributes[LandCls.estado] = 0;
                feature.attributes[LotCls.fuente] = codRequest;
                feature.attributes[LotCls.nomUser] = user;
                feature.attributes[LotCls.nomPc] = this.platformUpdate;
                feature.attributes[LotCls.anoCart] = new Date().getFullYear();
            });
            return features;
        },

        getDataOrigin(pointLotUrl, landUrl, lots) {
            // const self = this;
            const deferred = new Deferred();

            const promises = [
                this.getPointLotsOrigin(pointLotUrl, lots),
                this.getLandsOrigin(landUrl, lots)
            ]

            all(promises)
                .then(results => {
                    // self.currentPoinLotsRows = results[0].features;
                    // self.currentLandsRows = results[1].features;
                    return deferred.resolve(results);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        setParametersToAddFeatures(url, params) {
            return {
                url: `${url}/addFeatures`,
                content: {
                    features: JSON.stringify(params),
                    f: "json"
                },
                handleAs: "json",
                callbackParamName: "callback"
            };
        },

        setParametersToUpdateFeatures(url, params) {
            return {
                url: `${url}/updateFeatures`,
                content: {
                    features: JSON.stringify(params),
                    f: "json"
                },
                handleAs: "json",
                callbackParamName: "callback"
            };
        },

        setParametersToDeleteFeatures(url, params) {
            return {
                url: `${url}/deleteFeatures`,
                content: {
                    where: params,
                    f: "json"
                },
                handleAs: "json",
                callbackParamName: "callback"
            };
        },


        sendDataOriginToHistoric(config, currentLotsRows, currentPoinLotsRows, currentLandsRows) {
            const deferred = new Deferred();
            const promises = [];

            if (currentLotsRows) {
                const lotsHistoricRequestOptions = this.setParametersToAddFeatures(
                    config.lotHistoricUrl,
                    currentLotsRows
                );
                promises.push(esriRequest(lotsHistoricRequestOptions, { usePost: true }));
            }

            if (currentPoinLotsRows) {
                const pointsLotsHistoricRequestOptions = this.setParametersToAddFeatures(
                    config.pointLotHistoricUrl,
                    currentPoinLotsRows
                );
                promises.push(esriRequest(pointsLotsHistoricRequestOptions, { usePost: true }));
            }

            if (currentLandsRows) {
                const landsHistoricRequestOptions = this.setParametersToAddFeatures(
                    config.landHistoricUrl,
                    currentLandsRows
                );
                promises.push(esriRequest(landsHistoricRequestOptions, { usePost: true }));
            }


            // const promises = [
            //     esriRequest(lotsHistoricRequestOptions, { usePost: true }),
            //     esriRequest(pointsLotsHistoricRequestOptions, { usePost: true }),
            //     esriRequest(landsHistoricRequestOptions, { usePost: true })
            // ]

            all(promises)
                .then(results => {
                    return deferred.resolve(results);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        deleteDataOrigin(currentLotsRows, currentPoinLotsRows, currentLandsRows, config) {
            const deferred = new Deferred();

            const objetidLot = currentLotsRows.map(row => row.attributes.OBJECTID);
            const objetidPointLot = currentPoinLotsRows.map(row => row.attributes.OBJECTID);
            const objetidLand = currentLandsRows.map(row => row.attributes.OBJECTID);

            const lotsDeleteRequestOptions = this.setParametersToDeleteFeatures(
                config.lotUrl,
                `OBJECTID IN (${objetidLot.join(",")})`,
            );
            const pointsLotsDeleteRequestOptions = this.setParametersToDeleteFeatures(
                config.pointLotUrl,
                `OBJECTID IN (${objetidPointLot.join(",")})`,
            );
            const landsDeleteRequestOptions = this.setParametersToDeleteFeatures(
                config.landUrl,
                `OBJECTID IN (${objetidLand.join(",")})`,
            );

            const promises = [
                esriRequest(lotsDeleteRequestOptions, { usePost: true }),
                esriRequest(pointsLotsDeleteRequestOptions, { usePost: true }),
                esriRequest(landsDeleteRequestOptions, { usePost: true })
            ]

            all(promises)
                .then(results => {
                    return deferred.resolve(results);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        updateDataDeactivate(lands, config) {
            const deferred = new Deferred();
            const deactivateFeatures = this.setParametersToUpdateFeatures(
                config.landUrl,
                lands
            )

            esriRequest(deactivateFeatures, { usePost: true })
                .then(result => {
                    return deferred.resolve(result);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        addDataNew(lots, pointLots, lands, config) {
            const deferred = new Deferred();

            lots = Array.isArray(lots) ? lots : [lots]

            const lotNews = this.setParametersToAddFeatures(
                config.lotUrl,
                lots
            );

            pointLots = Array.isArray(pointLots) ? pointLots : [pointLots]

            const pointLotsNews = this.setParametersToAddFeatures(
                config.pointLotUrl,
                pointLots
            );

            lands = Array.isArray(lands) ? lands : [lands]

            const landsNews = this.setParametersToAddFeatures(
                config.landUrl,
                lands
            );

            const promises = [
                esriRequest(lotNews, { usePost: true }),
                esriRequest(pointLotsNews, { usePost: true }),
                esriRequest(landsNews, { usePost: true })
            ]

            all(promises)
                .then(results => {
                    return deferred.resolve(results);
                })
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        updateStatusRequests(lands, codRequests, caseRequest, ubigeo, config) {
            const deferred = new Deferred();
            const responseLands = UtilityCase.matchWithReceptionModel(lands)

            const response = {
                id: codRequests,
                results: responseLands,
                idType: parseInt(caseRequest)
            }
            for (let predio of response.results) {
                predio['ubigeo'] = ubigeo;
            }

            fetch(config.updateStatusApplication, {
                method: 'POST',
                body: JSON.stringify(response),
                headers: {
                    'Content-Type': 'application/json'
                }
            })
                .then(response => response.json())
                .then(data => deferred.resolve(data))
                .catch(err => deferred.reject(err));
            return deferred.promise;
        },

        checkLotsWithinLands(lots, lands) {
            for (let lot of lots) {
                const checkLands = []
                for (let land of lands) {
                    checkLands.push(geometryEngine.intersects(lot.geometry, land.geometry))
                }
                if (checkLands.every(i => i === false)) {
                    return false
                }
            }
            return true
        },

    };

    return UtilityCase;
});